import React, { useEffect, useState, Fragment } from "react";
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Buttons from './Buttons';

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '80vw',
      color: "#FFFFFF"
    },
  },
}));

export default function ValidationTextFields({setSearching}) {
  const classes = useStyles();
  const [searchData, setSearchData] = useState("");
  const [isSearch, setIsSearch] = useState(0);
  const [ihelperText, setHelperText] = useState("");

  const setSearch = (event) => {
    setSearchData(event.target.value);
  }

  const setValue = (event) => {
    if(event==0){
        setSearchData("");
        setSearching("");
    } else {
        if(searchData!=""){
            setSearching(searchData);
        } else {
            setHelperText("โปรดกรอกรหัสนักเรียน 5 หลัก");
        }
    }
    setIsSearch(event);
    setHelperText("");
  }

  return (
    <form className={classes.root} noValidate autoComplete="off">
      <div>
        <TextField
          id="outlined-error-helper-text"
          label="รหัสนักเรียน"
          defaultValue=""
          value={searchData}
          helperText={ihelperText}
          variant="outlined"
          onChange={setSearch}
        />
      </div>
      <Buttons setValue={setValue}></Buttons>
    </form>
  );
}