import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles((theme) => ({
  root: {
    '& .MuiTextField-root': {
      margin: theme.spacing(1),
      width: '80vw',
      color: "#FFFFFF"
    },
  },table: {
    minWidth: 650,
  },
}));

export default function BasicTable({data}) {
  const classes = useStyles();

  return (
    data ? (
    <TableContainer component={Paper} className={classes.root}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>ระดับชั้น</TableCell>
            <TableCell>เลขที่</TableCell>
            <TableCell align="center">รหัสนักเรียน</TableCell>
            <TableCell align="center">ชื่อ - สกุล</TableCell>
            <TableCell align="center">คะแนนสอบที่ได้&nbsp;(เต็ม 30 คะแนน)</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
            <TableRow key={data.id}>
              <TableCell component="th" scope="row">ม.5/{data.class}</TableCell>
              <TableCell align="center">{data.no}</TableCell>
              <TableCell align="center">{data.id}</TableCell>
              <TableCell align="left">{data.name}</TableCell>
              <TableCell align="center">{data.midterm}&nbsp;(ผลสอบ&nbsp;=&nbsp;"{data.passmid}")</TableCell>
            </TableRow>
        </TableBody>
      </Table>
    </TableContainer>) :
    <p>not found{data.length}</p>
  );
}
