import React, { useEffect, useState, Fragment } from "react";
import Tabletop from "tabletop";
import logo from './logo.svg';
import './App.css';
import BasicTable from './components/BasicTable'
import ValidationTextFields from './components/ValidationTextFields';
import Buttons from './components/Buttons';

function App() {
  const [data, setData] = useState([]);
  const [result, setResult] = useState([]);

  const setSearching = (e) => {
    setResult([]);
    data.map((item) => {
      if(e == parseInt(item.id)){
        setResult(item);
      } else {
      }
    })
  }

  useEffect(() => {
    Tabletop.init({
      key: "1ypKNFrzbiMKqTiyXAYpMgh5QRBmYF_xGgW9MfurE4hw",
      simpleSheet: true
    })
      .then((data) => setData(data))
      .catch((err) => console.warn(err));
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          ตรวจสอบผลคะแนนกลางภาค <br></br>
          ภาคเรียนที่ 2/2563&nbsp;&nbsp;รายวิชา ว32182 <br></br>
          การออกแบบและเทคโนโลยี <br></br>
          ชั้นมัธยมศึกษาปีที่ 5
        </p>
        <ValidationTextFields setSearching={setSearching}></ValidationTextFields>
        <div style={{width: "95vw"}}>
          <BasicTable data={result}></BasicTable>
        </div>
      </header>
    </div>
  );
}

export default App;
