
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import CloudUploadIcon from '@material-ui/icons/Send';
import KeyboardVoiceIcon from '@material-ui/icons/KeyboardVoice';
import SendIcon from '@material-ui/icons/Send';
import SearchIcon from '@material-ui/icons/Search';
import ClearIcon from '@material-ui/icons/Clear';
import SaveIcon from '@material-ui/icons/Save';


const useStyles = makeStyles((theme) => ({
    button: {
      margin: theme.spacing(1),
    },
  }));
  
  export default function Buttons({setValue}) {
    const classes = useStyles();

    const handleSelectedOK = (e) => {
      setValue(e);
    }
  
    return (
      <div>
        <Button
          variant="contained"
          color="secondary"
          className={classes.button}
          startIcon={<ClearIcon />}
          onClick={(event) => handleSelectedOK(0)}
        >
          ยกเลิก
        </Button>
        {/* This Button uses a Font Icon, see the installation instructions in the Icon component docs. */}
        <Button
          variant="contained"
          color="primary"
          className={classes.button}
          endIcon={<SearchIcon>send</SearchIcon>}
          onClick={(event) => handleSelectedOK(1)}
        >
          ค้นหา
        </Button>
      </div>
    );
  }